/* General Styles */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 20px;
  background-color: #f8f8f8; /* Fallback color */
  color: #222; /* Darken the text color a bit */

  /* Add background image properties */
  /* background-image: url('/back1.png'); 
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center; */
}

/* Header Styles */
header {
  background-color: #ffffff;
  color: #ffffff; /* Corrected to ensure text visibility */
  padding: 15px 0;
  text-align: center;
  background-image: url('/system1.png'); /* Adjust path if needed */
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;  /* Optional: Center the image */
}

header nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

header nav ul li {
  display: inline-block;
  margin-right: 20px;
}

header nav ul li a {
  color: #fff;
  text-decoration: none;
}

/* Main Content Styles */
main {
  max-width: 800px;
  margin: 30px auto;
}

.tutorial-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
}

.tutorial-card {
  border: 1px solid #ddd;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);

  /* Translucent white background */
  background-color: rgba(255, 255, 255, 0.7); /* Adjust opacity as needed */
}

.tutorial-card h2 {
  margin-top: 0;   
}

.tutorial-card a {
  background-color: #428bca;
  color: #fff;
  padding: 10px 15px;
  text-decoration: none;
  border-radius: 5px;
}
