/* About.css */
.about-container {
    padding: 2rem;
    max-width: 800px;
    margin: 0 auto;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .about-container h1 {
    font-size: 2.5rem;
    margin-bottom: 1rem;
  }
  
  .about-container p {
    font-size: 1.2rem;
    line-height: 1.6;
    margin-bottom: 1rem;
  }
  
  .about-container h2 {
    font-size: 2rem;
    margin-top: 2rem;
    margin-bottom: 1rem;
  }
  
  .about-container ul {
    list-style-type: disc;
    padding-left: 2rem;
  }
  
  .about-container ul li {
    font-size: 1.2rem;
    margin-bottom: 0.5rem;
  }
  