/* General Styles - Light Mode */
body {
    font-family: 'Consolas', 'Courier New', monospace; /* Code-like font */
    margin: 0;
    padding: 20px;
    background-color: #ffffff; /* Light background */
    color: #333; /* Dark text color */
}

/* Content Styling */
h1, h2 {
    color: #336699; /* Slightly darker color for headings */
}

p  {
    margin-top: 10px; 
    margin-bottom: 20px;
} 

ol {
    list-style: decimal; /* Numbered list for steps */
}

li {
    margin-bottom: 5px;
}

a, a:visited {
    color: #2075c9; /* Link Color */
}

/* Code Blocks */ 
pre {
    background-color: #eee; 
    padding: 15px;
    border-radius: 5px;
    overflow-x: auto;  /* Add horizontal scroll for long code lines */
    white-space: pre-wrap; /* Preserve line breaks in code */
}

/* Optional: Styling for inline code snippets if you use them */
code {
    background-color: #eee; 
    color: #336699;
    padding: 2px 5px;
    border-radius: 3px;
}
